        <!---->
        <!--R01-->
        <el-dialog :visible.sync="Dialog_R01">
          <span>
            <div class="heading_title">
              SPORTLER KSEKUCHEN
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="https://i.bmp.ovh/imgs/2020/04/75c6542f7159581c.png"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Andre Duda
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Hallo liebe Angelique Kerber ! Ich habe fr Dich ein
                      Rezept herausgesucht, was zu dir als Sportlerin passt und
                      du auch genieen kannst in dieser, leider erzwungenen,
                      tennisfreien Zeit ! Und zwar einen SPORTLER KSEKUCHEN !
                      Dieser ist arm an Kohlehydraten und ein ideales Gericht
                      der Low Carb Ernhrungsform ! Ich wnsche Dir sportlichen
                      Genu und guten Appetit mit Deiner Familie und deinen
                      Freunden liebe Angie ! Alles Gute, bleib gesund, pass auf
                      dich auf und wir Fans hoffen dich bald wieder auf den
                      Courts dieser Welt zu sehen und das bald das geplante
                      Gruppentreffen von uns mit dir stattfinden kann ! Liebe
                      Gre von deinem Fan Andre Duda
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >4Stk. Eier</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250g Frischkse</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >500g Magertopfen (Magerquark)</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >50g Proteinpulver</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1Spr. Vanillearoma</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >15g Sstoff</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1Schuss Rum</el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont distance"></i>Zeit
            </div>
            <div>
              <el-card
                class="card_time"
                shadow="never"
              >60 min. Gesamtzeit</el-card>
              <el-card
                class="card_time"
                shadow="never"
              >15 min. Zubereitungszeit</el-card>
              <el-card
                class="card_time"
                shadow="never"
              >45 min. Kochzeit (Backzeit)</el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i>Heizen Sie den Backofen auf 200
                Grad Ober-Unterhitze vor. Trennen Sie die Eier in Dotter und
                Eiklar und schlagen das Eiklar zu steifem Schnee. Nun verrhren
                Sie den Topfen und den Frischkse mit den Eidottern, dem Rum und
                dem Sstoff, am Besten mit dem Handmixer.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i>Nun rhren Sie das Proteinpulver
                sowie das Vanillearoma in die Masse und heben zum Schluss den
                Eischnee vorsichtig unter.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i>Nun wird die Ksekuchen-Masse in
                eine Silikonform gefllt und im Backofen bei 180 Grad fr etwa
                45 Minuten gebacken.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i>Der Kuchen schmeckt am besten,
                wenn er vollstndig ausgekhlt ist und kalt serviert wird!
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R02-->
        <el-dialog :visible.sync="Dialog_R02">
          <span>
            <div class="heading_title">
              Ksekuchen
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="https://i.bmp.ovh/imgs/2020/04/aca24460d8e7ae3a.png"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Denny Dinauer
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Ich habe fr das Kochbuch, auch mal fr zwischendurch ein Rezept fr einen tollen Ksekuchen
                    </div>
                    <div>Viele Gre</div>
                    <div>Denny Dinauer
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">Fr den Teig</div>
            <el-card
              class="card_tag"
              shadow="never"
            >150 g Mehl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >65 g Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >65 g Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Ei(er)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL Backpulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >etwas Salz
            </el-card>
            <div class="heading_subtitle">Fr den Baiserbelag
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >3 Eiwei
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >100 g Zucker
            </el-card>
            <div class="heading_subtitle">Fr den Belag
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >750 g Quark (Magerquark)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >150 g Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1  Pck. Puddingpulver (Vanille)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >500 ml Milch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 Eigelb
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Pck. Vanillezucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            > Zitrone(n), den Saft davon
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Tasse l
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Alle Zutaten fr den Teig zu einem Knetteig verarbeiten. In einer gefetteten Springform (26 cm) verteilen und einen kleinen Rand hoch drcken. Alle Zutaten fr den Belag verrhren und 45 Minuten bei 200C backen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Fr das Baiser das Eiwei steif schlagen und den Zucker dabei einrieseln lassen. Auf die Ksefllung streichen, dabei 1 cm Rand lassen. Die Masse mit einer Stricknadel durchstechen und noch mal 20 Minuten bei 160C backen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Den Springformrand erst lsen, wenn der Kuchen gut abgekhlt ist. Die Pudding-Ksemasse fliet sonst von dannen. Die Trnchen auf der Baisermasse kommen mit der Zeit ganz automatisch, lasst euch berraschen. Der Kuchen lsst sich wunderbar schon einen Tag vorher zubereiten.
              </div>
            </el-card>
          </span>
        </el-dialog>
