            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >1 1/4 cups (200g) chopped dates
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 1/4 (310ml) water
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 teaspoon bicarbonate soda
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >60g butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3/4 cup (165g) caster sugar
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 eggs
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 cup (150g) self- raising flour
            </el-card>
            <div class="heading_subtitle">Butterscotch sauce</div>
            <el-card
              class="card_tag"
              shadow="never"
            >1 1/2 cups (300g) firmly packed brown sugar
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250g unsalted butter, chopped
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >300ml thickened cream
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> grease deep 19cm square cake pan & line base with baking paper.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> combine dates and water in pan, bring to boil.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> remove dates and water from heat and add soda, stand for 5 minutes.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> beat butter and sugar in a small mixing bowl with electric mixer until combined.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> beat in eggs 1 at a time, beat until combined.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> gently whisk in sifted flour then the date mixture.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-7"></i> pour mixture into prepared pan and bake in a moderate oven (approx 180 degrees) for 50 minutes.
              </div>
            </el-card>
            <div class="heading_subtitle">Butterscotch sauce method</div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> combine all ingredients in a saucepan and stir over medium heat without boiling.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> when sugar is dissolved simmer stirring for 5 minutes.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> serve with vanilla ice cream or a dollop of cream.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R05-->
        <el-dialog :visible.sync="Dialog_R05">
          <span>
            <div class="heading_title">
