                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >500 g Hackfleisch, gemischt
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 Stange/n Lauch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250 g Schmelzkse
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Becher Crme frache, ca. 150 g
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 Wrfel Gemsebrhe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >700 ml Wasser
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 kleine Baguette(s) zum Aufbacken
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz und Pfeffer, aus der Mhle
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 EL l
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Muskat
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Knoblauchpulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Zwiebelpulver
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Den Ofen auf 175 C Ober-/Unterhitze vorheizen. Die Baguettes in den vorgeheizten Ofen legen und ca. 10 Minuten backen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Derweil l in einen groen Topf geben. Das Hackfleisch darin von allen Seiten gut anbraten und mit Salz und Pfeffer wrzen. Den Lauch in kleine Ringe schneiden und zum Hackfleisch geben. Ca. 5 Minuten mit anbraten.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i>Das Wasser zugieen, Brhwrfel hineingeben und alles ca. 10 Minuten auf kleiner Flamme kcheln lassen. Den Schmelzkse einrhren und schmelzen lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i>Crme frache einrhren und noch einmal kurz aufkochen lassen. Die Suppe mit Salz, Pfeffer, Muskat, Knoblauch und Zwiebelpulver krftig abschmecken.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Die Baguettes in Scheiben schneiden und zu der Suppe reichen.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R06-->
        <el-dialog :visible.sync="Dialog_R06">
          <span>
