        </el-dialog>
        <!--R07-->
        <el-dialog :visible.sync="Dialog_R07">
          <span>
            <div class="heading_title">
              Orecchiette pasta with turnip green
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                >
                  <div class="avatar_text">AS</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Armando Soave
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      The Orecchiette with turnip green, is a traditional recipe from the Puglia region and Taranto City, and the most famous dish of the Apulian cuisine, maybe! We Apulians call it "Orecchiette alle cime di rapa"
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont camera"></i>Foto</div>
            <div>
              <el-card>
                <el-image
                  style="width:100%"
                  src="https://ftp.bmp.ovh/imgs/2020/04/d217bedbf4591ca9.jpg"
                ></el-image>
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">For two people
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >300 gr of orecchiette pasta(fresh pasta)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250 gr of turnip greens
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 garlic cloves
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >half red chilli
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >extra virgin olive oil (Evo Olive Oil)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >salt
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Wash turnip green under lukewarm water and then remove stems.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Put some water in a big pot, add the salt, and let it boil, as soon as the water starts to boil, put the orecchiette pasta ans green turnips in the water, and cook
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> In a aluminum or non-stick pan, add some evo oli(the amount about 3 or 4 spoons), the garlic cloves that have been peeled and crushed, the chili peppers that have been broken into pieces. Turn off the heat when the garlic begins to brown.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Drain le orecchiette e turnip green, when they are still undercooked, very al dente.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Turn the heat back up high under the pan with the evo oil, garlic cloves and chili peppers, and add the orecchiette and turnip green. so Stir continuously until all ingredients have fully cooked and have been flavored by the oil.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> remove the garlic clove, and serve at the dinner table
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-7"></i> Optional note:To personal taste, in point 3 you can add some anchovies in oil.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R08-->
        <el-dialog :visible.sync="Dialog_R08">
          <span>
            <div class="heading_title">
              KLASSISCHES JGERSCHNITZEL
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >750g frische Champignons
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >50g Bacon
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 klein gehackte Zwiebeln
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250ml Gemsebrhe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250ml Sahne
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1TL getrockneter Thymian
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Bund Petersilie
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            > TL Salz
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Pfeffer
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >n.B. Milch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >4 Schweineschnitzel (Oberschale)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Mehl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1Eigelb
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Semmelbrsel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Butterschmalz
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Zuerst die Schnitzel waschen, gut trocken tupfen und dnn klopfen. Mit Salz und Pfeffer grozgig wrzen. Dann in Mehl, geschlagenen Eigelb und Paniermehl wenden und in einer Pfanne von beiden Seiten im Butterschmalz goldgelb braten. Anschlieend die Schnitzel aus der Pfanne nehmen und warm stellen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Wenn ntig wieder etwas Fett in die Pfanne geben und die klein gehackte Zwiebel anbraten. Wenn die Zwiebeln leicht brunen, die Champignons wieder zugeben und mit der Brhe und der sen Sahne auffllen. Salzen und Pfeffern und den getrockneten Thymian dazugeben. Die Sauce jetzt unter gelegentlichem Rhren so lange einkcheln, bis die Flssigkeit deutlich weggekocht ist. (Das kann je nach Temperatur bis zu 15 Minuten dauern).
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Dann unter Rhren so viel Milch zugeben, bis die Sauce die gewnschte cremige Konsistenz erreicht hat. Die Sauce sollte nicht zu dnn sein und eine rahmige Konsistenz haben. Die Pfanne vom Herd nehmen und  der gehackten Petersilie unter die Sauce heben. Eventuell noch mit Salz und Pfeffer nachwrzen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Jetzt die Schnitzel auf die Teller geben und mit der Sauce bergieen. Die verbliebene Petersilie zur Dekoration ber die Sauce geben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Dazu passen sehr gut Pommes Frites oder Kartoffelkroketten.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R09-->
