                    </div>
                    <div>Liebe Angie, wir vermissen Dich momentan alle sehr. Heute wre ich fr 1 Woche nach Stuttgart gefahren und htte Dich sicher auch gesehen.Ich wollte mich mit vielen Leuten aus der Gruppe treffen und gemeinsam wollten wir Dich anfeuern. Leider hat das Virus alles verhindert. Berlin, Bad Homburg berall hatte ich Karten, mein Urlaub war auf Tennis ausgerichtet. Ich bin ziemlich traurig. Nun mssen wir alle warten. Wie ich das Tennisspielen und das Anschauen Deiner Matches vermisse. Kann es kaum erwarten, Dich und die Anderen wieder spielen zu sehen.
