                    <div>It's been a long time. Can't wait to see you in the tournaments next year. The last time I was very lucky to support you at this year's AO. What you dont know is that, it was on Chinese New Years Day, and my country was paralyzed by the virus and I couldnt go home. I was painful then, but you won the match. You took a selfie with us, the match and your warm words saved me.
                    </div>
                    <div>I am happy to create this lovely project with Jorgen. Maybe I was young, maybe the tennis period I experienced was not perfect, but I dont know what the better looks like actually. After all, even when tennis is temporarily lost, we can still unite like this, to find some new fun of life.
                    </div>
                    <div>Stay healthy and stay positive please. From the past to the future, ANGELIQUE KERBER always represent the power of sports in my heart.
                    </div>
                    <div>Best wishes from China,
                    </div>
                    <div>Lorraine Liu
                    </div>
                  </div>
                </el-card>
              </el-col>
