                <i class="iconfont icon-2"></i> Die Eier trennen. Aus Eigelb, Butter, der Hlfte des Rohrzuckers, Mehl, Backpulver, der ausekratzten Vanilleschote und Milch einen Teig kneten und in eine eingefettete Springform geben. Den Rhabarber auf dem Teig verteilen und im auf 180 C vorgeheizten Ofen etwa 30 Minuten backen, bis der Teig zu brunen beginnt.
