                    <div>hier ist ein Rezept aus einem Kochbuch fr Singles. Das ist sehr praktisch, weil man dann keine Reste hat, die man im Khlschrank vergisst und nach ein paar Tagen wegschmeien muss. Wenn du fr mehrere Personen kochen willst - vielleicht will die Petko oder so ja mal vorbeikommen - einfach entsprechend grere Mengen nehmen.
                    </div>
                    <div>Wenn man nicht meine favorisierte Alternative mit Schinken nimmt ist es vegetarisch. Muskatnuss ist nicht so meins, stattdessen nehme ich lieber Krutersalz. Einfach ausprobieren was dir besser schmeckt.
                    </div>
                    <div>Das Foto stammt brigens von einem Gossip Konzert, bei dem ich in der ersten Reihe stand. Das war eine Aktion vom Radiosender 1Live. Ich hatte die Karten, die man nicht kaufen konnte, gewonnen, wir hatten uns nach vorne gekmpft und dann war Beth Ditto erkltet und nach 20 Minuten nur Talk wieder weg. Schade, aber trotzdem ein schnes Erlebnis.
                    </div>
                    <div>Ich hoffe auch dich irgendwann mal live und in Farbe und vor allem gesund und munter sehen zu drfen.
                    </div>
                    <div>Viel Spa mit diesem und den anderen Rezepten. Enjoy.
                    </div>
