                >
                  <div class="text_content">
                    <div>
                      Hallo Angie Kerber,
                    </div>
                    <div>herzliche Gre, sende ich Dir mit einem Rezept aus meiner badischen Heimat, dass leicht nachzukochen ist und ausgesprochen lecker schmeckt.
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Stk. Zwiebeln
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >125 g roher Schinken
            </el-card>
            <el-card
              class="card_tag"
