                >
                  Mario Westphal
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Hallo Angie,
                    </div>
                    <div>ich bin ein groer Fan von dir und dank dir habe ich mit dem Tennis begonnen, zwar war ich schon 20 Jahre alt, aber ich bin dir sehr dankbar, dass du das Interesse in mir fr diesen tollen Sport geweckt hast.
                    </div>
                    <div>Ich werde zwar nicht mehr der grte Tennisspieler, aber ich bin schon zufrieden, wenn der Ball im gegnerischen Spielfeld landet. 
                    </div>
                    <div>Da ich kein groer Koch bin, aber ein Fan von guten Salaten, mchte ich dir das Rezept fr den besten Nudelsalat nicht vorenthalten.
                    </div>
                    <div>Fr mich darf dieser Nudelsalat beim Grillen nicht fehlen und ich hoffe, dass er dir auch so schmeckt wie mir.
                    </div>
                    <div>Mit lieben Gren
                    </div>
                    <div>Mario
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Glasgurkentaler
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 groe rote Paprika
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 kleine Dose Mais
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Packung Kochschinken
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >ca. 500g Magerjoghurt
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Glas Miracel Whip Classic
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Petersilie oder Schnittlauch (tiefgefroren)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >500 g Nudeln (Schleifen oder Spirellis)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Zwiebeln
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Pfeffer</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Paprikapulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >etwas Zucker
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Die Nudeln 2 min weniger kochen als die Anleitung vorgibt.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Die Flssigkeit der Gurkentaler in eine Schssel geben und die Paprika klein schneiden und dazu geben. Ebenfalls den Mais in die Schssel geben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Wenn die Nudeln fertig gekocht sind, diese warm dazugeben. Anschlieend mit Salz, Pfeffer, Paprikapulver und etwas Zucker wrzen. Die Nudeln mssen im Sud fter umgerhrt werden. Dann den Salat erkalten lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Nun den Kochschinken in Wrfeln, den Joghurt und die Mayonnaise hinzugeben. Alles miteinander vermischen und anschlieend die Petersilie oder den Schnittlauch dazu geben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Dann den Nudelsalat etwas durchziehen lassen und nochmal abschmecken.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R44-->
        <el-dialog :visible.sync="Dialog_R44">
          <span>
            <div class="heading_title">
              Nussecken
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                >
                  <div class="avatar_text">SE</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Sebastian
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Hallo Angie,
                    </div>
                    <div>ich hatte mich schon so darauf gefreut dich in Bad Homburg live Tennis spielen zu sehen. Leider wurde das Turnier ja auch abgesagt. Aber aufgeschoben ist ja bekanntlich nicht aufgehoben :-)
                    </div>
                    <div>Zur berbrckung bis zu deinem nchsten Match habe ich dir mein Lieblingsrezept vorbereitet.
                    </div>
                    <div>Als Single in der Coronazeit habe ich mich im Backen ausprobiert und ich muss sagen, dass es mir richtig Spa macht.
                    </div>
                    <div>Viele Gre aus Mnnerstadt aus dem schnen Frankenland in Bayern,
                    </div>
                    <div>Sebastian
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">FR DEN TEIG
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >25 g Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >100 g Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Eier
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >300 g Mehl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL Backpulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Pck. Vanillezucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >etwas Butter fr die Form
            </el-card>
            <div class="heading_subtitle">FR DEN BELAG
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >200 g Aprikosenmarmelade
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >175 g Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >130 g Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Pck. Vanillezucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >400 g gemahlene Nsse
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >200 g Zartbitterkuvertre
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Backofen auf 180 Grad (Umluft: 160) vorheizen. Ein Backblech (39x26x4 cm) gut einfetten.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Fr den Teig Butter, Zucker, Vanillezucker und Eier miteinander verkneten. Mehl, Backpulver miteinander vermischen und mit dem restlichen Teig verkneten. Teig in das Backblech geben und zurecht drcken, damit ein ebener Boden entsteht (Alternativ mit dem Nudelholz ausrollen und dann reinlegen).
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Fr den Belag Aprikosenmarmelade mit einem Lffel auf dem Teig verteilen und glattstreichen. Butter, Zucker und Vanillezucker in einem Topf schmelzen. Gemahlene Nsse und 4 EL Wasser hinzugeben und unter stndigem Rhren kurz kcheln lassen, sodass eine feuchte Masse entsteht. Nussmasse auf dem Blech verteilen und ebenfalls glatt streichen. Nussecken im vorgeheizten Ofen circa 25-27 Minuten backen. Kurz abkhlen lassen. Den noch warmen Kuchen in Ecken schneiden. Dazu in Quadrate schneiden und diese dann ber die Ecken halbieren. Ecken vollstndig auskhlen lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Zartbitterkuvertre grob hacken und im Wasserbad schmelzen. In ein kleines, hohes Gef umfllen. Nussecken aus der Form neben und die spitzen Ecken jeweils in die Schokolade tunken. Ecken dann auf ein Backpapier legen und die Schokolade trocknen lassen.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R45-->
        <el-dialog :visible.sync="Dialog_R45">
          <span>
            <div class="heading_title">
              CRUMBLE
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
