            >
              <div>
                <i class="iconfont icon-2"></i> Die Haferflocken, den braunen Zucker, das Mehl und die Butter vermengen bis sie gleichmige Streusel bilden.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Die Frchte in einer feuerfesten Glas- oder Keramikschale von 20x20cm Seitenlnge verteilen und mit der Mischung bedecken.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Ohne Deckel ca. 30-45 Minuten im Ofen backen bis die Frchte kochen und in der Mitte ganz gar sind. Lauwarm servieren.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R46-->
        <el-dialog :visible.sync="Dialog_R46">
          <span>
            <div class="heading_title">
              Pasta mit grnem Spargel und Pistazien in Zitronen-Buttersoe
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">PORTIONEN 4 Personen
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >400 g Pasta nach Wahl - z.B. Penne 8-10 Min. Kochzeit
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >500 g grner Spargel - gewaschen und in ca. 2-3 cm groe Stcke geschnitten
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Bio-Zitrone - Abrieb
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >50 g Pistazien mit Schale, gerstet, ohne Salz - Aus der Schale nehmen und grob hacken
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >60 g Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Knoblauchzehen - fein geschnitten
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >30 g Basilikum - grob gehackt
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Meersalz, schwarzer Pfeffer und Zitronensaft - zum Abschmecken Parmsan, gerieben - zum Servieren
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Zitronenscheiben und etwas -abrieb - zum Servieren
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont distance"></i>Zeit
            </div>
            <div>
              <el-card
                class="card_time"
                shadow="never"
              >GESAMT 30 Minuten
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> In einem sehr groen Topf (in den der Spargel auch noch hinein passt) bringst Du erst einmal das Salzwasser zum kochen, gibst die Pasta hinein und stellst die Kochzeit ein.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Bis das Pasta-Wasser kocht schon einmal die Pistazien aus der Schale lsen und grob hacken sowie den Basilikum klein schneiden. Die Bio-Zitrone hei abwaschen und die Schale abreiben sowie den Knoblauch schlen und fein schneiden.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Den grnen Spargel waschen, die holzigen Enden (ca. 2 cm) abschneiden und schrg und nach Belieben in ca. 2-3 cm groe Stcke schneiden. Der grne Spargel kommt die letzten 5 Minuten der Nudelkochzeit zu den Nudeln in das Kochwasser.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Die Butter zerlsst Du auf mittlerer Stufe in einer groen Pfanne (Nudeln und Spargel mssen hinein passen) und dnstest den Knoblauch darin an bis er schn duftet. Er darf aber nicht zu dunkel werden sonst wird er bitter.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Nach der Kochzeit die Nudeln mit dem Spargel abgieen und zu der Knoblauch- Butter in die Pfanne geben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> Jetzt gibst Du die restlichen Zutaten hinein: nmlich die gehackten Pistazien, den Zitronenschalenabrieb und das Basilikum. Alles miteinander vermengen und mit Meersalz und schwarzem Pfeffer krftig wrzen sowie mit Zitronensaft abschmecken.Ich wnsche Dir einen guten Appetit!
              </div>
            </el-card>
          </span>
        </el-dialog>
