                  Frank Strbrauck
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Als ich die letzten beiden Male in Brasilien war, habe ich immer wieder in den Restaurants diesen leckeren brasilianischen Fischeintopf gegessen: Moqueca. Besonders im Nordosten des Landes, in Bahia, ist das Gericht weit verbreitet. Viel Spa beim Kochen!
                    </div>
