          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >ca. 600 Gramm Kabeljau, Barsch oder Lachs
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >ca. 200-300 Gramm Krabben
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Dose zerhackte Tomaten (400 Gramm)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 Zwiebeln
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
