            >Salz, Pfeffer, Zucker, scharfer Paprika zum Abschmecken
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Zunchst macht ihr euch an den Fisch und die Krabben. Diese werden erst einmal aufgetaut und anschlieend fr 30 Minuten in eine Marinade aus Limettensaft, der Hlfte des Koreanders und zwei ausgepressten Knoblauchzehen gelegt.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Jetzt gehts ans Schnibbeln: Zwiebeln in Wrfel und Paprika in Streifen schneiden. Whrenddessen knnt ihr schon einmal die Pfanne oder den Kochtopf mit dem Palml und Olivenl hei machen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Anschlieend die brigen zwei Knoblauchzehen darin auspressen und die klein gehackten Zwiebeln glasig anbraten. Anschlieend kommen die Tomatenstcke, und das Paprika hinzu, dann die Kokosmilch und der Fisch sowie die Krabben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Nun aufkochen lassen, danach auf mittlerer Stufe weiterkcheln, circa 30 Minuten lang.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Wer es gern schrfer mag, kann Chilichoten mitkcheln lassen. Mehr als vier Schoten sollten es aber nicht sein! Zur Moqueca schmeckt wunderbar Reis. Guten Appetit!
              </div>
            </el-card>
          </span>
        </el-dialog>
