                    <div>anbei ein Rezept aus meiner Heimat Baden-Wrttemberg. Fr die Maultaschen gibt es viele Rezepte. Meistens werden diese von einer Generation auf die andere weitergegeben.
                    </div>
                    <div>Das ursprngliche Rezept stammt von meiner Oma. Meine Mama und spter dann auch ich, haben einige nderungen vorgenommen, so dass das Rezept fr die heutige Zeit geeignet ist. Viel Spa beim Nachkochen.
                    </div>
                    <div>Lieben Gru
                    </div>
                    <div>Uschi Rosenbauer
                    </div>
