          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >500 g Nudelteig (Schmidts No 1 ist fr mich der beste)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >400 g Hackfleisch
            </el-card>
            <el-card
              class="card_tag"
