            >100 g gerauchte Schinkenwurst (klein wrfeln)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >100 g magerer Bauch kleine Wrfel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Bund Petersilie
            </el-card>
            <el-card
              class="card_tag"
