            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Brtchen einweichen, Lauchzwiebeln und Petersilie kleinhacken und in Butter andnsten, anschlieend den aufgetauten Spinat dazugeben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Die Brtchen ausdrcken und kleinzupfen mit dem Hackfleisch, dem Brt, Schinkenwurst, Bauch, den Eiern und den angednsteten Lauchzwiebeln, Petersilie und dem Spinat vermengen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Den Fleischteig mit Salz und Pfeffer wrzen. Den Fleischteig gleichmig auf den mit Eiwei bestrichenen Nudelteig auf eine Hlfte streichen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Die andere Hlfte darber klappen und die Rnder gut festdrcken. Darauf achten, dass sich keine Luftblasen bilden.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Mit einem Teigrdchen die Maultaschen in der gewnschten Gre ausrdeln. In sprudelndes Salzwasser geben und 10 Minuten ziehen lassen.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R50-->
        <el-dialog :visible.sync="Dialog_R50">
          <span>
