          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Die Hefe mit etwas lauwarmer Milch und einem Teelffel Zucker auflsen und 10 Minuten ruhen lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Dann das Mehl mit der aufgeweichten Butter, den Eiern und der restlichen Milch, Zucker und der angemachten Hefe gut vermengen und den Teig schlagen bis er Blasen wirft und sich von der Schssel lst.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Den Hefeteig mit einem Tuch zugedeckt an einem warmen Ort gehen lassen. Dann den Teig 1,5 cm dick ausrollen, mit einem Glas ausstechen und nochmals gehen lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Eine Pfanne gut ausfetten, 1-2 Tassen Wasser und eine Brise Salz zum kochen bringen und die Nudeln einlegen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Die Pfanne zudecken und bei schwacher Hitze die Nudeln aufziehen lassen.Die Garzeit betrgt 15-20 Minuten. Ist das Wasser verdampft wird der Deckel entfernt, so dass keine Wassertropfen auf die fertigen Dampfnudeln fallen.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R51-->
        <el-dialog :visible.sync="Dialog_R51">
          <span>
            <div class="heading_title">
              Bigos (original polnischer Sauerkrauteintopf)
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                >
                  <div class="avatar_text">Sanny</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Sanny Wolf
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Hallo liebe Angie, ich mchte dir gerne ein polnisches Rezept Schicken(es muss ja nicht immer deutsche Kche sein :)).Ich sehe dich gerne Spielen ,leider ist es inmoment nicht mglich aber ich glaube fest daran das du nochmals einen Grand Slam Titel gewinnen kannst. Ich freue mich nchtest Jahr auf Stuttgart auf dich und dann wrde ich gerne ein Selfie machen lg Sanny aus Braunschweig
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont camera"></i>Foto</div>
            <div>
              <el-card>
                <el-image
                  style="width:100%"
                  src="https://ftp.bmp.ovh/imgs/2020/04/2fb65ea11145c2fe.jpg"
                ></el-image>
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">Portionen 10</div>
            <el-card
              class="card_tag"
              shadow="never"
            >400 g Sauerkraut frisches
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >280 g Weikohl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >400 g Fleisch Rind, Schwein, Geflgel gemischt
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >120 g Wurst Krakauer, Schlesische Wurst oder Kabanossi
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >0.40 Scheibe gerucherter Speck
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >4 getrocknete Pflaumen
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 g getrockenete Pilze
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1.20 Zwiebeln mittelgro
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >200 ml Wasser
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1/5 Brhwrfel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >60 ml Rotwein
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >4 Pimentkrner
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >0.80 Lorbeerbltter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Wacholderbeeren
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >0.80 EL Schweineschmalz
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >0.80 pfel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Tomatenmark</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Kmmel</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Majoran</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Pfeffer</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz</el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Ein bis zwei Stunden vor dem Beginn des Kochens schon einmal die getrockneten Pilze (am besten Waldpilze) sowie die Trockenpflaumen in jeweils einem eigenen Gef einweichen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Zu Beginn das Sauerkraut in ca. 5 cm lange Stcke schneiden und in einem Sieb gut abtropfen lassen. Den Weikohl in hnlich groe Stcke schneiden.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Das Fleisch in nicht allzu kleine Stcke schneiden (z.B. 1-2 cm), das Schweineschmalz in einem groen Topf erhitzen und das Fleisch anbraten (keine extra Pfanne verwenden, damit der Bratensatz spter in das Bigos kommt). Dabei nur so viel Fleisch gleichzeitig in den Topf geben, dass immer alles nebeneinander Platz hat und lieber in mehreren Portionen nacheinander anbraten (damit das Fleisch nicht so viel Wasser zieht).
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Den Speck in Streifen schneiden, die Zwiebeln und pfel schlen und wrfeln. Ist alles Fleisch angeboten, etwas das Fett entfernen (aber nicht den Bratensatz) und im Bratenfond Speck, Zwiebeln und pfel anschwitzen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Die Pilze und Pflaumen aus dem Einweichwasser nehmen, eventuell noch etwas kleinschneiden und dazu geben. Sobald die Zwiebeln glasig sind, kommt das Sauerkraut und der Weikohl hinzu.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> Ist der Weikohl zusammen gefallen, kommt der Rotwein, Piment, Lorbeerbltter, Wacholderbeeren, Kmmel und der Majoran mit in den Topf. Das Tomatenmark (oder alternativ zwei frische Tomaten) hinzugeben und auch das Einweichwasser der Pilze durch eine Kaffeefilter in den Topf laufen lassen. Mit Salz und Pfeffer wrzen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-7"></i> Jetzt beginnt das Kochen: einen Deckel auf den Topf und in den auf 150C geheizten Ofen stellen. Dort kocht das ganze etwa 2 bis 2,5 Stunden. Zur Sicherheit ab und zu in den Topf schauen, damit unten nichts anbrennt (ggfls. noch etwas Flssigkeit zugeben). Das Fleisch sollte nach dem Kochen auch ganz weich sein.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-8"></i> Jetzt nochmal ohne Deckel fr eine halbe Stunde auf den Herd, damit Flssigkeit verkochen kann. Jetzt auch Wrste in den Topf geben, damit sie hei werden (evtl. einpicksen, damit das Fett in den Eintopf laufen kann).
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R52-->
