            >1-2 EL COCA-COLA
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >COLA Flschchen aus Weingummi
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont distance"></i>Zeit
            </div>
            <div>
              <el-card
                class="card_time"
                shadow="never"
              >Zubereitungszeit: 30 Minuten
              </el-card>
              <el-card
                class="card_time"
                shadow="never"
              >Backzeit: 35 Minuten
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Den Backofen auf 160 Grad vorheizen. Die Papierfrmchen in die Mulden der Muffinform oder auf einem Backblech bereitstellen. Das Mehl in eine Schssel geben und grndlich mit dem Backpulver, dem Zimt, den Nssen und den Schokoraspeln mischen.
