        <!--R56-->
        <el-dialog :visible.sync="Dialog_R56">
          <span>
            <div class="heading_title">
              Gefllte Paprikaschoten mit Reis
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="https://i.postimg.cc/Kz7m37r5/image.png"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Samantha Pietzarka
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Liebe Angie,
                    </div>
                    <div>mein Name ist Samantha Pietzarka und ich wohne derzeit in Hamburg. Ich verfolge deine Karriere schon seit einigen Jahren und bin ein groer Fan von dir. Ich bewundere dich als Spielerin sehr, aber vor allem als Menschen Angie. Du bist trotz deines Erfolges immer auf dem Teppich geblieben und hast dich nie unterkriegen lassen, obwohl es Phasen in deinem Leben gab die nicht einfach waren. Aber du hast immer gekmpft und hast nie die Menschen in deinem Leben vergessen. Du hast mir mit deiner Art und mit deinen unermdlichen Kmpfen auf und neben dem Tennisplatz immer viel gezeigt und zwar vor allem nicht auf zu geben. Du bist jemand der seine eigenen Entscheidungen trifft und das Wichtigste ist dabei, dass du dich dabei wohl und glcklich fhlst. Weist du bis ich auf meine Gefhle hren konnte hat es eine ganze Weile gedauert und ich hatte viele Tiefschlge, aber dann sehe ich Personen wie du die niemals aufgeben und zu dem stehen was sie sagen und das gibt einem dann auch selber die Kraft.
                    </div>
                    <div>Und so viel kann ich nur sagen: solange du dabei glcklich bist bin ich es als Fan mit dir zusammen.
                    </div>
                    <div>Du hast mit diesem Ehrgeiz und einfach weil du auf dem Boden geblieben bist schon so viele Ziele erreicht und hast schon viele Pokale gewonnen und du warst auch die Nummer 1 der Welt. Ich wei du hattest eine schwierige Phase und Corona macht es momentan alles fr uns nicht einfacher, aber jetzt heit es zusammen halten und aufeinander aufpassen, aber auch jemanden eine kleine Freude zu machen. So wie wir es gerade bei dir hoffentlich machen.
                    </div>
                    <div>Unsere Facebook Gruppe besser unser Leiter Hr. Jrgen Nissen hat den tollen Vorschlag gemacht dir ein Kochbuch zu erstellen, da du ja wie wir gehrt haben die freie Zeit frs Kochen nutzt. Und ich finde diese Idee sehr gut. Vor allem da ich, wenn ich die Mglichkeit habe, es auch probiere neben dem alltglichen Stress.
                    </div>
                    <div>Ich hoffe sehr fr dich, dass du fr dich schne viele tolle Ideen findest und dich das auch einfach ablenkt von der allgemeinen Situation. Meine Ablenkung ist derzeit mein Umzug und die dazugehrige Planung. Alles nicht so einfach, aber man muss alles Stck fr Stck machen und dann auf das Beste hoffen. brigens ich htte dich sehr gerne in Berlin gesehen und die Chance gehabt dich vielleicht auch live zu treffen. Hatte mir auch eine Karte frs ganze Turnier gekauft. Mal sehen vielleicht wird es ja etwas im neuen Jahr. Sofern du dann nach Berlin kommst und wir alle die Corona Krise berstanden haben. Wrde mich sehr darber freuen.
                    </div>
                    <div>Das waren jetzt ein paar Zeilen von mir und bitte bleibe gesund und einfach so wie du bist.
                    </div>
                    <div>Pass gut auf dich und deine Familie auf und geniee diese Zeit zum Kraft tanken und einfach fr dich sein. Und sobald es wieder los geht werden wir dich untersttzen und dann neue schne Erlebnisse zusammen haben und vielleicht sehen wir uns dann ja auch mal von Angesicht zu Angesicht.
                    </div>
                    <div>Ganz liebe Gre
                    </div>
                    <div>Samantha Pietzarka
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont camera"></i>Foto</div>
            <div>
              <el-card>
                <el-image
                  style="width:100%"
                  src="https://ftp.bmp.ovh/imgs/2020/04/375e41bc7db8a526.png"
                ></el-image>
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">Fr 4 Personen</div>
            <el-card
              class="card_tag"
              shadow="never"
            >4 Paprikaschoten
            </el-card>
            <div class="heading_subtitle">Fr die Fllung</div>
            <el-card
              class="card_tag"
              shadow="never"
            >350 g gemischtes Hackfleisch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 altbackenes Brtchen
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 kleine Zwiebel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Knoblauchzehe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL Paprikapulver edels
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Ei
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL Salz
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Pfeffer</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Petersilie</el-card>
            <div class="heading_subtitle">Fr die Soe</div>
            <el-card
              class="card_tag"
              shadow="never"
            >1 kleine Zwiebel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Knoblauchzehe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Tomaten
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >300 ml passierte Tomaten
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 EL l
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL Oregano
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Pfeffer</el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i>Die Paprikaschoten waschen und am Stielende vorsichtig einen Deckel abschneiden. Die Kerne, sowie die weien Scheidewnde mit einem kleinen Messer entfernen. Die Schote mit Wasser aussplen. Die Schotenunterseiten eventuell mit einem groen Messer begradigen, damit diese nachher gerade in der Auflaufform stehen. Dabei darf allerdings kein Loch in der Schotenunterseite entstehen. Die abgetrennten Deckel aufbewahren. Diese werden spter fr die Soe bentigt. Da die gefllte Paprika mit Hackfleisch im Backofen zubereitet wird, sollte der Backofen jetzt schon vorgeheizt werden (200 C).
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i>Fr die Zubereitung der Hackfleischfllung zuerst das Brtchen in einer kleinen Schssel mit Wasser einweichen. Die Zwiebel, sowie den Knoblauch fein wrfeln bzw. den Knoblauch durch eine Knoblauchpresse drcken und die Petersilie fein hacken. Das Hackfleisch, das Ei, die Zwiebelwrfel, die Petersilie und die zerkleinerte Knoblauchzehe in eine Schssel geben. Das eingeweichte Brtchen gut ausdrcken und ebenfalls hinzufgen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i>Nun die Fllung mit Paprikapulver, Salz und Pfeffer wrzen und mit den Hnden zu einer homogenen Masse vermengen. Die Paprikaschoten mit dieser Hackfleischmasse gleichmig fllen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i>Fr die Zubereitung der Soe fr die gefllte Paprika mit Hackfleisch die vorhin aufbewahrten Paprikaschotendeckel vom Stiel entfernen und in kleine Stcke schneiden. Die Zwiebel und die Knoblauchzehe fein wrfeln. Die Tomaten zerkleinern.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i>Nun in einer Pfanne l erhitzen und darin die Zwiebel- und Paprikawrfel sowie die zerkleinerte Knoblauchzehe fr ca. 2 Minuten anbraten, dabei die Zutaten gelegentlich umrhren. Jetzt das Paprikapulver einrhren und die Tomatenstcke dazugeben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i>Als nchstes die passierte Tomaten hinzugieen. Die Soe mit Oregano, Salz, Pfeffer und eventuell gekrnter Brhe wrzen. Dann die Soe in eine Auflaufform fllen und die Paprikaschoten in die Soe hineinsetzen. Die gefllte Paprika mit Hackfleisch auf der mittleren Schiene bei 200 C ca. 60 Minuten backen. Als Beilage zur gefllten Paprika mit Hackfleisch passen Reis oder auch Nudeln.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R57-->
        <el-dialog :visible.sync="Dialog_R57">
          <span>
