                  <div class="avatar_text">Ralf</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Ralf Holzenkamp
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                    </div>
                    <div>Liebe Angelique Kerber
                    </div>
